nav {
    background: var(--color-white);
    border: 2px solid;
    height: max-content;
    display: flex;
    /* padding: 0.7rem 1.7rem; */
    margin: auto;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 1rem; 
    /* gap: 0.8rem;   */
    /* backdrop-filter: blur(15px);  */
}

nav a {
    background: transparent;
    padding: 1rem;
    display: block;
    font-size: 1.15rem;
    text-align: center;
}

nav a:hover{
    background: var(--color-grey);
}

/* MEDIA QUERIES (TABLET DEVICES) */
@media screen and (max-width: 1024px) {

    nav {
        /* left: -1rem;
        transform: translate(50%);
        bottom: auto;
        top: 2rem; */
        /* transform: translatey(100%); */
        /* margin-left: 0.5rem; */
    }
    nav a {
        font-size: 1.5rem;
        padding: 0.5rem 2rem;
    }

    .nav-text {
        visibility: hidden;
        display: none;
    }
}

/* MEDIA QUERIES (MOBILE DEVICES) */
@media screen and (max-width: 600px) {

    nav {
        /* left: auto;
        top: auto;
        bottom: 1rem; */
    }
    
    nav a {
        font-size: 1.5rem;
        padding: 0.5rem 1rem;
    }
}